.about_container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}
.about_me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}
.about_me img{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition:var(--transation);
}
.about_me img:hover{
    transform: rotate(0deg);
}
.about_cards{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.5rem;
}
.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition:var(--transation);
}
.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}
.about_content p{
    margin: 2rem 0 2.6rem;
    color:var(--color-light);
}
/*Tablets*/
@media screen and (max-width:1024px) {
.about_container{
  grid-template-columns: 1fr;
  gap: 0;
}
.about_me{
    width: 50%;
    margin: 2rem auto 4rem;
}
.about_content p{
    margin: 1rem 0 1.5rem;
}
}
/*Mobiles*/
@media screen and (max-width:600px) {
 .about_container{
  grid-template-columns: 1fr;
  gap: 0;
}
.about_me{
    width: 65%;
    margin: 0rem auto 3rem;
}
.about_cards{
    grid-template-columns: 1fr 1fr;
}
.about_content{
    text-align: center;
}
.about_content p{
    margin: 1.5rem 0;
}
}